















{ user, server } = require('src/db')

module.exports =
  name: 'signinModal'

  data: ->
    credentials:
      email: ''
      password: ''

  computed:
    userName: -> return @$store.state.currentUser.username
    userExists: ->
      if @userName != 'stranger'
        return true
      else return false

  methods:
    close: -> @$store.commit('SET_SIGNIN', false)
    logout: -> @$store.dispatch 'logout'
    login: ->
      @$store.dispatch 'login', @credentials
      setTimeout =>
        @close()
      , 1500

