














module.exports =

  name: 'app'
  components:
    InfoPanel: require './components/InfoPanel'
    SignUpPanel: require './components/SignUpPanel'
    SigninModal: require './components/SigninModal'

  computed:
    signin: -> @$store.state.signin
  methods:
    setSignin: -> @$store.commit('SET_SIGNIN', true)




